.newCost {
  background-color: rgb(144, 207, 197);
  padding: 1rem;
  margin: 1rem auto;
  width: 70rem;
  height: 88px;
  max-width: 95%;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.25);
}

.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addButton {
  display: flex;
  justify-content: flex-end;
  margin-right: 15px;
}
